import React from 'react'
import { ErrorMessage } from 'components/form/controls/common'
import {
  Container,
  InputWrapper,
  Label,
  InputComponent,
  IconWrapper,
} from './Input.styled'

type InputProps = React.InputHTMLAttributes<HTMLInputElement> & {
  error?: string | undefined
  icon?: React.ReactNode | undefined
}

const Input: React.FC<InputProps> = props => {
  const {
    value,
    type = 'text',
    title,
    disabled,
    error,
    onChange,
    onBlur,
    icon,
  } = props

  return (
    <Container>
      <InputWrapper hasIcon={!!icon}>
        {icon && <IconWrapper hasError={!!error}>{icon}</IconWrapper>}
        <Label hasValue={!!value} hasError={!!error}>
          {title}
        </Label>
        <InputComponent
          value={value}
          type={type}
          disabled={disabled}
          autoComplete='off'
          autoCapitalize='off'
          spellCheck='false'
          autoCorrect='off'
          onChange={onChange}
          onBlur={onBlur}
        />
      </InputWrapper>
      <ErrorMessage error={error} />
    </Container>
  )
}

export default Input
