import React, { useEffect } from 'react'
import { useDispatch } from 'react-redux'
import { push } from 'connected-react-router'
import { toast } from 'react-toastify'
import { SessionUtils } from 'utils'
import { PageLayout } from 'layouts'
import { PageLayoutSegmentProps } from 'layouts/types'
import { Button } from 'components/form/controls'
import { logoutUser } from 'modules/Auth/slices/auth'
import trainingLibraryRoutes from 'modules/TrainingLibrary/routing/routes'


type DemoProps = PageLayoutSegmentProps & {
  sessionToken?: string
  onLogout: () => void
  onOpenTrainingLibrary: () => void
  onShowToast: () => void
}

const Demo: React.FC<DemoProps> = ({
  sessionToken,
  onLogout,
  onOpenTrainingLibrary,
  onShowToast,
}) => {
  return (
    <div style={{ color: '#ffffff', textAlign: 'center' }}>
      <h1>Session token:</h1>
      <div>{sessionToken}</div>
      <br />
      <Button title='Logout' onClick={onLogout} />
      <br />
      <br />
      <Button title='TrainingLibrary' onClick={onOpenTrainingLibrary} />
      <br />
      <br />
      <Button title='Show Toast' onClick={onShowToast} />
    </div>
  )
}

const Home: React.FC = () => {
  const dispatch = useDispatch()
  const sessionToken = SessionUtils.getSessionToken() || undefined

  useEffect(() => {
    toast.success("Login successful. Please choose your course")
  }, [])

  function logout() {
    dispatch(logoutUser())
  }

  function openTrainingLibrary() {
    dispatch(push(trainingLibraryRoutes.trainingLibrary))
  }

  function showToast() {
    toast.success("Success", { delay: 100 });
    setTimeout(() => toast.info("Info"), 500);
    setTimeout(() => toast.warn('Warning', {
      hideProgressBar: true,
      autoClose: false,
      closeOnClick: false,
      }), 1000);
    setTimeout(() => toast.warn(
      (<div>Custorm warning<br /><br />
          <p>Some description</p><br />
          <button onClick={() => alert('click1')}>my button</button>
          <button onClick={() => alert('click2')}>my button</button>
        </div>
      ), {
        hideProgressBar: true,
        autoClose: false,
        closeOnClick: false,
        style: { background: "grey", border: "2px solid white" }
      },
    ), 1000);
    setTimeout(() => toast.error("Error",
      { closeOnClick: false, hideProgressBar: true, autoClose: false, position: 'top-right' }), 1500);
    setTimeout(() => toast('ðŸ¦„ Icon default!'), 2000);
    setTimeout(() => toast.dark('ðŸ¦„ Dark'), 2500);
  }

  return (
    <PageLayout>
      <Demo
        sessionToken={sessionToken}
        onLogout={logout}
        onOpenTrainingLibrary={openTrainingLibrary}
        onShowToast={showToast}
        segmentId='content'
      />
    </PageLayout>
  )
}

export default Home
