import styled from 'styled-components'
import { LabelProps, InputWrapperProps, IconWrapperProps } from './types'
import { ErrorWrapper } from 'components/form/controls/common/ErrorMessage/ErrorMessage.styled'

export const Container = styled.div`
  position: relative;

  ${ErrorWrapper} {
    margin-top: -2px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }
`

export const Label = styled.label<LabelProps>`
  position: absolute;
  top: 50%;
  left: 20px;
  transform: translateY(-50%);
  transition: all 0.3s ease;
  ${({ theme }) => `
    font-size: ${theme.fontSize.desktop.small};
    color: ${theme.color.grey};
  `}

  ${({ hasValue, theme }) =>
    hasValue &&
    `
    transform: translateY(-120%);
    font-size: ${theme.fontSize.desktop.tiny};
  `}

  ${({ hasError, theme }) =>
    hasError &&
    `
    color: ${theme.color.danger};
  `}
`

export const InputComponent = styled.input`
  width: 100%;
  height: 60px;
  padding: 27px 20px 13px;
  outline: 0;
  background: transparent;
  box-sizing: border-box;
  border: 0;
  line-height: 20px;
  ${({ theme }) => `
    font-weight: ${theme.fontWeight.bold};
    font-size: ${theme.fontSize.desktop.small};
    color: ${theme.color.black};
  `}
`

export const IconWrapper = styled.div<IconWrapperProps>`
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  left: 10px;
  top: 10px;
  width: 40px;
  height: 40px;

  svg {
    width: 20px;
    height: 20px;
    g {
      transition: all 0.3s ease;
    }
  }

  ${({ hasError, theme }) =>
    hasError &&
    `
    svg g {
      stroke: ${theme.color.danger};
    }
  `}
`

export const InputWrapper = styled.div<InputWrapperProps>`
  width: 100%;
  height: 60px;
  position: relative;
  ${({ theme }) => `
    background: ${theme.color.white};
    border-radius: ${theme.borderRadius.base};
  `}

  &:focus-within {
    ${Label} {
      transform: translateY(-120%);
      ${({ theme }) => `
        font-size: ${theme.fontSize.desktop.tiny};
        color: ${theme.color.oceanLight};
      `}
    }

    ${IconWrapper} {
      svg g {
        stroke: ${({ theme }) => theme.color.oceanLight};
      }
    }
  }

  ${({ hasIcon }) =>
    hasIcon &&
    `
    ${InputComponent} { padding-left: 60px; }
    ${Label} { left: 60px; }
  `}
`
