import React from 'react'
import { Button as StyledButton } from './Button.styled'

type ButtonType = 'button' | 'reset' | 'submit'

type ButtonProps = {
  title: string
  disabled?: boolean
  onClick?: () => void
  type?: ButtonType
  fill?: boolean
}

const Button: React.FC<ButtonProps> = props => {
  const { title, disabled, onClick, type, fill } = props
  return (
    <StyledButton
      isFilled={fill}
      type={type}
      disabled={disabled}
      onClick={onClick}
    >
      {title}
    </StyledButton>
  )
}

export default Button
